type ButtonProps = {
  children: string;
  // styles: React.CSSProperties
  // onClick: (event: React.MouseEvent<HTMLButtonElement>) => void
  class: string
};

const Button = (props: ButtonProps) => {
  return <button className={props.class}>{props.children}</button>;
};
 
export default Button;

